import React, { Component, Fragment } from 'react';
import Toast from '../../plugins/toast';
import Codes from '../../components/codes';
import Button from '../../plugins/general/button';

export default class toast1 extends Component{
  constructor(props){
    super(props);

    this.state = {
      example: `
Toast({
  msg: string,
  delay: number,
  fn: callback
});
      `,
      show:`
Toast({
  msg: 'hello world'
});
      `,
      show1: `
Toast({
  msg: '2s',
  delay: 2000
});
      `,
      show2: `
Toast({
  msg: '1s, ',
  fn(){
    Toast({
      msg: ''
    });
  }
});
      `
    }
  }

  show(){
    Toast({
      msg: 'hello world'
    });
  }

  show1() {
    Toast({
      msg: '2s',
      delay: 2000
    });
  }

  show2() {
    Toast({
      msg: '1s, ',
      fn(){
        Toast({
          msg: ''
        });
      }
    });
  }

  render(){
    const { test } = this.state;
    return(
      <Fragment>
        <h1>toast</h1>
        <blockquote>
          <p>: </p>
        </blockquote>
        <h5 id="">:</h5>
        <Codes codes={this.state.example} />
        <ul>
          <li><code>msg</code> <strong></strong></li>
          <li><code>delay</code>, <code>1000ms</code></li>
          <li><code>fn</code></li>
          <li>? : <code>xumeng0611@gmail.com</code></li>
        </ul>
        <h3> {test}</h3>
        <p>1., </p>
        <Button type="default" onClick={e => { this.show() }}>click</Button>
        <Codes codes={this.state.show} />
        <p>2., </p>
        <Button type="default" onClick={e => { this.show1() }}>click</Button>
        <Codes codes={this.state.show1} />
        <p>3.</p>
        <Button type="default" onClick={e => { this.show2() }}>click</Button>
        <Codes codes={this.state.show2} />
      </Fragment>
    )
  }
}


